import Notification from 'core/notification';
import ModalFactory from 'core/modal_factory';
import ModalEvents from 'core/modal_events';
import Template from "core/templates";

/**
 * Create Modal debug dialog
 *
 * @param {Object} data
 * @return {Promise<void>}
 */
export const createModalDebug = async(data) => {
    try {
        const modal = await ModalFactory.create({
            title: data.title,
            body: Template.render('local_competvet/modal_debug', {
                'content': data.content,
                'debugs': data.debugs
            }),
            type: ModalFactory.types.CANCEL,
            large: true
        });
        modal.show();
        return modal;
    } catch (error) {
        await Notification.exception(error);
    }
};

/**
 * Create Modal debug dialog from event
 *
 * @param {Object} event
 * @return {Promise<void>}
 */
export const createModalDebugFromEvent = async(event) => {
    const data = {...event.detail};
    const modal = await createModalDebug(data);
    modal.getRoot().on(ModalEvents.hidden, () => {
        if (data.returnurl) {
            window.location.assign(event.detail.returnurl);
            window.location.reload();
        }
    });
};
